* {
  box-sizing: border-box;
}

html {
  height: 100%;
  font-size: 100%;
  line-height: 1.3;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
  font-family: Arial, Tahoma, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white;
}

article,
aside,
footer,
header,
nav,
section {
  display: block;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
  -webkit-text-decoration-skip: objects;
  background-color: transparent;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font-family: Arial, Tahoma, sans-serif;
  font-size: 100%;
  line-height: inherit;
}

input[type="button"],
input[type="submit"],
button {
  background-color: white;
  cursor: pointer;
}

b,
strong {
  font-weight: inherit;
}

figcaption,
figure,
main {
  display: block;
}

figure {
  margin: 0;
}

hr {
  box-sizing: content-box;
  overflow: visible;
  height: 0;
}

pre {
  font-family: monospace, monospace;
  font-size: 1rem;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1rem;
}

dfn {
  font-style: italic;
}

mark {
  background-color: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25rem;
}

sup {
  top: -0.5rem;
}

audio,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

img {
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: rem-calc(1) dotted ButtonText;
}

fieldset {
  padding: 0;
}

legend {
  display: table;
  padding: 0;
  max-width: 100%;
  color: inherit;
  white-space: normal;
}

progress {
  display: inline-block;
  vertical-align: baseline;
}

textarea {
  overflow: auto;
  resize: none;
}

[type="checkbox"],
[type="radio"] {
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline: none;
}

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

::selection {
  background: #b3d4fc;
  text-shadow: none;
}

details,
menu {
  display: block;
}

summary {
  display: list-item;
}

canvas {
  display: inline-block;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

body {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: nowrap;
  height: 100%;
  text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  font-family: Futura, sans-serif;
  font-size: 16px;
  color: #212121;
}

main {
  flex: 1 0 auto;
  width: 100%;
  background: white;
}

.container {
  margin: 0 auto;
  padding: 0 14px;
  width: 100%;
  max-width: 1200px;
}

.header {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  font-family: Futura, sans-serif;
  background: #282828;
}

.header-bg {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
}

.header > .container {
  height: 100%;
}

.header-inner {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 25px 0;
  width: 100%;
  color: white;
}

.header-logo {
  opacity: 0.9;
  font-size: 25px;
  text-transform: uppercase;
}

.header-logo > a {
  color: currentColor;
}

.header-logo__text > span:first-child {
  border: 0.12em solid currentColor;
  border-radius: 0.05em;
  padding: 0.2em;
}

.header-logo__text > span:last-child {
  margin-left: 0.15em;
  font-size: 0.8em;
  font-weight: 400;
}

.header-nav__list {
  display: flex;
}

.header-nav__item + .header-nav__item {
  margin-left: 2em;
}

.header-nav__item > a {
  color: currentColor;
}

.footer {
  flex: 0 0 auto;
}

