.header {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  font-family: Futura, sans-serif;
  background: rgb(40, 40, 40);
  &-bg {
    position: absolute;
    display: block;
    top: 0;
    left: 0; }
  & > .container {
    height: 100%; }
  &-inner {
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 25px 0;
    width: 100%;
    color: rgb(255, 255, 255); }
  &-logo {
    opacity: 0.9;
    font-size: 25px;
    text-transform: uppercase;
    & > a {
      color: currentColor; }
    &__text {
      & > span:first-child {
        border: 0.12em solid currentColor;
        border-radius: 0.05em;
        padding: 0.2em; }
      & > span:last-child {
        margin-left: 0.15em;
        font-size: 0.8em;
        font-weight: 400; } } }
  &-nav {
    &__list {
      display: flex; }
    &__item {
      & + .header-nav__item {
        margin-left: 2em; }
      & > a {
        color: currentColor; } } } }
